<template>
  <div class="upload-cover" @click="showCoverSelect">
    <div class="cover-wrap">
      <img class="cover-image" ref="cover-image" :src="value">
    </div>

    <el-dialog title="" :visible.sync="dialogVisible" append-to-body>
      <el-tabs v-model="activeName" type="card">
        <el-tab-pane label="" name="first">
          <image-list :is-show-add='false' :is-show-action="false" is-show-selected ref="image-list" />
        </el-tab-pane>
        <el-tab-pane label="" name="second">
          <input type="file" ref="file" @change="onFileChange">
          <img width="100" ref="preview-image">
        </el-tab-pane>
      </el-tabs>
      <span slot="footer" class="dialog-footer">
        <el-button @click="dialogVisible = false"> </el-button>
        <el-button type="primary" @click="onCoverConfirm"> </el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
import { uploadImage } from '@/api/image'
import ImageList from '@/views/image/components/image-list'

export default {
  name: 'UploadCover',
  components: {
    ImageList
  },
  props: ['value'],
  data () {
    return {
      activeName: 'second',
      dialogVisible: false
    }
  },
  computed: {},
  watch: {},
  created () {},
  mounted () {},
  methods: {
    showCoverSelect () {
      // 
      this.dialogVisible = true
    },
    onFileChange () {
      // 
      const file = this.$refs.file.files[0]
      // 
      const blob = window.URL.createObjectURL(file)
      this.$refs['preview-image'].src = blob
    },
    onCoverConfirm () {
      // tab,input-file,
      if (this.activeName === 'second') {
        const file = this.$refs.file.files[0]
        if (!file) {
          this.$message('')
          return
        }
        // 
        const fd = new FormData()
        fd.append('image', file)
        uploadImage(fd).then(res => {
          // 
          this.dialogVisible = false
          // 
          this.$emit('input', res.data.data.url)
        })
      } else if (this.activeName === 'first') {
        // 
        const imageList = this.$refs['image-list']
        const selected = imageList.selected
        if (selected === null) {
          this.$message('')
          return
        }
        // 
        this.dialogVisible = false
        // 
        this.$emit('input', imageList.images[selected].url)
      }
    }
  }
}
</script>

<style scoped lang='less'>
.cover-wrap {
  width: 150px;
  height: 150px;
  border: 1px solid #ccc;
  .cover-image {
    height: 120px;
    max-width: 100%;
  }
}
</style>
