<template>
  <div class="login-container">
    <div class="login-form-wrap">
      <div class="login-head">
        <div class="logo"></div>
      </div>
      <!-- form:
         1.el-formmodel
         2.el-form-itemprop(prop)
         3.el-formrules
       -->
      <el-form class="login-form" ref="login-form"  :model="user" :rules="formRules">
        <el-form-item prop="mobile">
          <el-input v-model="user.mobile" placeholder=""></el-input>
        </el-form-item>
        <el-form-item prop="code">
          <el-input v-model="user.code" placeholder=""></el-input>
        </el-form-item>
        <el-form-item prop="agree">
          <el-checkbox v-model="user.agree"></el-checkbox>
        </el-form-item>
        <el-form-item>
          <el-button class="login-btn" type="primary" :loading="loginLoading" @click="onLogin"></el-button>
        </el-form-item>
      </el-form>
    </div>
  </div>
</template>

<script>
import { login } from '@/api/user'

export default {
  name: 'LoginIndex',
  components: {},
  props: {},
  data () {
    return {
      user: {
        mobile: '', // 
        code: '', // 
        agree: false
      },
      // checked: false, // 
      loginLoading: false, // loading
      formRules: {
        //  :[]
        mobile: [
          { required: true, message: '', trigger: 'change' },
          { pattern: /^1[3|5|7|9]\d{9}$/, message: '', trigger: 'change' }
        ],
        code: [
          { required: true, message: '', trigger: 'change' },
          { pattern: /^\d{6}$/, message: '' }
        ],
        agree: [
          {
            // :callback()
            // :callback(new Error('))
            validator: (rule, value, callback) => {
              if (value) {
                callback()
              } else {
                callback(new Error(''))
              }
            },
            trigger: 'change'
          }
        ]
      }
    }
  },
  computed: {},
  watch: {},
  created () {},
  mounted () {},
  methods: {
    onLogin () {
      // ()
      // const user = this.user

      // 
      // validate
      this.$refs['login-form'].validate(valid => {
        // ,
        if (!valid) {
          return
        }
        // ,
        this.login()
      })
    },
    login () {
      //  loading...
      this.loginLoading = true
      // ,
      login(this.user).then(res => {
        console.log(res)

        // 
        this.$message({
          message: '',
          type: 'success'
        })

        // loading
        this.loginLoading = false

        // ,,JSON
        window.localStorage.setItem('user', JSON.stringify(res.data.data))

        // 
        // this.$router.push('/')
        this.$router.push({ name: 'home' })
      }).catch(err => {
        console.log('', err)
        this.$message.error(',')

        // loading
        this.loginLoading = false
      })
    }
  }
}
</script>

