<template>
  <div class="article-container">
    <el-card class="filter-card">
      <div slot="header" class="clearfix">
        <!--  -->
        <el-breadcrumb separator-class="el-icon-arrow-right">
          <el-breadcrumb-item to="/"></el-breadcrumb-item>
          <el-breadcrumb-item></el-breadcrumb-item>
        </el-breadcrumb>
      </div>
      <!--  -->
      <el-form ref="form" :model="form" label-width="50px" size="mini">
      <el-form-item label=" :">
        <el-radio-group v-model="status">
          <!-- el-radiolabelvalue -->
          <el-radio :label="null"></el-radio>
          <el-radio :label="0"></el-radio>
          <el-radio :label="1"></el-radio>
          <el-radio :label="2"></el-radio>
          <el-radio :label="3"></el-radio>
          <el-radio :label="4"></el-radio>
        </el-radio-group>
      </el-form-item>
      <el-form-item label=" :">
        <el-select v-model="channelId" placeholder="">
          <el-option label="" :value="null"></el-option>
          <el-option :label="channel.name" :value="channel.id" v-for="(channel, index) in channels" :key="index"></el-option>
        </el-select>
      </el-form-item>
      <el-form-item label=" :">
        <el-date-picker v-model="rangeDate" type="datetimerange" start-placeholder="" end-placeholder="" :default-time="['12:00:00']" format="yyyy-MM-dd" value-format="yyyy-MM-dd"></el-date-picker>
      </el-form-item>
      <el-form-item>
        <!-- buttonclick,, -->
        <el-button type="primary" :disabled="loading" @click="loadArticles(1)"></el-button>
      </el-form-item>
      </el-form>
    </el-card>
    <el-card class="box-card">
      <div slot="header" class="clearfix"> {{ totalCount }} </div>
      <!--  -->
      <!-- table 
        1tabledata v-for,
        2 el-table-column width: label: prop:
        3(,)
       -->
      <el-table :data="articles" style="width: 100%" class="list-table" size="mini" v-loading="loading">
        <el-table-column prop="date" label="">
          <template slot-scope="scope">
            <img v-if="scope.row.cover.images[0]" class="article-cover" :src="scope.row.cover.images[0]" alt="">
            <img v-else class="article-cover" src="./no-img.gif" alt="">
          </template>
        </el-table-column>
        <el-table-column prop="title" label=""></el-table-column>
        <el-table-column prop="status" label="">
          <!--  template  slot-scope="scope" -->
          <template slot-scope="scope">
            <el-tag :type="articleStatus[scope.row.status].type">{{ articleStatus[scope.row.status].text }}</el-tag>
            <!-- <el-tag v-if="scope.row.status === 0" type="warning"></el-tag>
            <el-tag v-else-if="scope.row.status === 1"></el-tag>
            <el-tag v-else-if="scope.row.status === 2" type="success"></el-tag>
            <el-tag v-else-if="scope.row.status === 3" type="danger"></el-tag>
            <el-tag v-else-if="scope.row.status === 4" type="info"></el-tag> -->
          </template>
        </el-table-column>
        <el-table-column prop="pubdate" label=""></el-table-column>
        <el-table-column label="">
          <!--  template  -->
          <template slot-scope="scope">
            <el-button size="mini" type="primary" circle icon="el-icon-edit" @click="$router.push('/publish?id=' + scope.row.id.toString())"></el-button>
            <el-button size="mini" circle type="danger" icon="el-icon-delete" @click="onDeleteArticle(scope.row.id)"></el-button>
          </template>
        </el-table-column>
      </el-table>
      <!--  -->
      <!-- , -->
      <!--
        total: 10
        page-size:
      -->
      <el-pagination layout="prev, pager, next" :total="totalCount" :page-size="pageSize" :disabled="loading" :current-page.sync="page" background @current-change="onCurrentChange"  />
    </el-card>
  </div>
</template>

<script>
import { getArticles, getArticleChannels, deleteArticle } from '@/api/article'

export default {
  name: 'ArticleIndex',
  components: {},
  props: {},
  data () {
    return {
      form: {
        name: '',
        region: '',
        date1: '',
        date2: '',
        delivery: false,
        type: [],
        resource: '',
        desc: ''
      },
      articles: [], // 
      articleStatus: [
        { status: 0, text: '', type: 'info' },
        { status: 1, text: '', type: '' },
        { status: 2, text: '', type: 'success' },
        { status: 3, text: '', type: 'warning' },
        { status: 4, text: '', type: 'danger' }
      ],
      totalCount: 0, // 
      pageSize: 10, // 
      status: null, // 
      channels: [], // 
      channelId: null, // 
      rangeDate: null, // 
      loading: true, // 
      page: 1 // 
    }
  },
  computed: {},
  watch: {},
  created () {
    this.loadArticles(1)
    this.loadChannels()
  },
  mounted () {},
  methods: {
    loadArticles (page = 1) {
      // 
      this.loading = true
      getArticles({
        page,
        per_page: this.pageSize,
        status: this.status,
        channel_id: this.channelId,
        begin_pubdate: this.rangeDate ? this.rangeDate[0] : null,
        edd_pubdate: this.rangeDate ? this.rangeDate[1] : null
      }).then(res => {
        // console.log(res)
        // this.articles = res.data.data.results
        const { results, total_count: totalCount } = res.data.data
        this.articles = results
        this.totalCount = totalCount
        // 
        this.loading = false
      })
    },
    onSubmit () {
      console.log('submit!')
    },
    onCurrentChange (page) {
      this.loadArticles(page)
    },
    loadChannels () {
      getArticleChannels().then(res => {
        // console.log(res)
        this.channels = res.data.data.channels
      })
    },
    onDeleteArticle (articleId) {
      // console.log(articleId)
      // console.log(articleId.toString())
      this.$confirm('?', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      }).then(() => {
        deleteArticle(articleId.toString()).then(res => {
          // ,
          this.loadArticles(this.page)
          this.$message({
            message: '',
            type: 'success'
          })
        })
      }).catch(() => {
        this.$message({
          message: '',
          type: 'info'
        })
      })
    }
  }
}
</script>

<style scoped lang='less'>
.filter-card {
  margin-bottom: 25px;
}

.list-table {
  margin-bottom: 20px;
}

.article-cover {
  width: 60px;
  background-size: cover;
}
</style>
