<template>
  <div class="comment-container">
    <el-card class="filter-card">
      <div slot="header" class="clearfix">
        <!--  -->
        <el-breadcrumb separator-class="el-icon-arrow-right">
          <el-breadcrumb-item to="/"></el-breadcrumb-item>
          <el-breadcrumb-item></el-breadcrumb-item>
        </el-breadcrumb>
      </div>

      <!--  -->
      <!-- table 
        1tabledata v-for,
        2 el-table-column width: label: prop:
        3(,)
       -->
      <el-table class="table-list" :data="articles" style="width: 100%">
        <el-table-column prop="title" label=""></el-table-column>
        <el-table-column prop="total_comment_count" label=""></el-table-column>
        <el-table-column prop="fans_comment_count" label=""></el-table-column>
        <el-table-column prop="address" label="">
          <template slot-scope="scope"> {{scope.row.comment_status ? '' : ''}} </template>
        </el-table-column>
        <el-table-column prop="address" label="">
          <template slot-scope="scope">
            <el-switch v-model="scope.row.comment_status" active-color="#13ce66" inactive-color="#ff4949" :disabled="scope.row.statusDisabled" @change="onStatusChange(scope.row)"></el-switch>
          </template>
        </el-table-column>
      </el-table>

      <!--  -->
      <el-pagination style="margin-top:20px;" @size-change="handleSizeChange" @current-change="handleCurrentChange" :current-page.sync="page" :page-sizes="[20, 40, 80, 100]" :page-size.sync="pageSize" layout="total, sizes, prev, pager, next, jumper" :total="totalCount" background />
    </el-card>
  </div>
</template>

<script>
import { getArticles, updateCommentStatus } from '@/api/article'

export default {
  name: 'CommentIndex',
  components: {},
  props: {},
  data () {
    return {
      articles: [], // 
      totalCount: 0, // 
      pageSize: 10, // 
      page: 1 // 
    }
  },
  computed: {},
  watch: {},
  created () {
    this.loadArticles()
  },
  mounted () {},
  methods: {
    handleSizeChange () {
      this.loadArticles(1)
    },
    handleCurrentChange (page) {
      // ,
      this.loadArticles(page)
    },
    loadArticles (page = 1) {
      // 
      this.page = page
      getArticles({
        response_type: 'comment',
        page,
        per_page: this.pageSize
      }).then(res => {
        // console.log(res)
        const { results } = res.data.data
        results.forEach(article => {
          article.statusDisabled = false
        })
        this.articles = results
        this.totalCount = res.data.data.total_count
      })
    },
    onStatusChange (article) {
      // 
      article.statusDisabled = true
      //   
      updateCommentStatus(article.id.toString(), article.comment_status).then(res => {
        // 
        article.statusDisabled = true
        this.$message({
          type: 'success',
          message: article.comment_status ? '' : ''
        })
      })
    }
  }
}
</script>

<style scoped lang='less'>
.tabel-list {
  margin-bottom: 30px;
}
</style>
