<template>
  <div class="settings-container">
    <el-card class="box-card">
      <div slot="header" class="clearfix">
        <!--  -->
        <el-breadcrumb separator-class="el-icon-arrow-right">
          <el-breadcrumb-item to="/"></el-breadcrumb-item>
          <el-breadcrumb-item></el-breadcrumb-item>
        </el-breadcrumb>
      </div>
      <el-row>
        <el-col :span="15">
          <el-form ref="form" :model="user" label-width="100px" :rules="formRules">
            <el-form-item label=" :"> {{ user.id }} </el-form-item>
            <el-form-item label=" :"> {{ user.mobile }} </el-form-item>
            <el-form-item label=" :" prop="name">
              <el-input v-model="user.name"></el-input>
            </el-form-item>
            <el-form-item label=" :" prop="intro">
              <el-input type="textarea" v-model="user.intro"></el-input>
            </el-form-item>
            <el-form-item label=" :" prop="email">
              <el-input v-model="user.email"></el-input>
            </el-form-item>
            <el-form-item>
              <el-button type="primary" :loading="updateProfileLoading" @click="onUpdateUser"></el-button>
            </el-form-item>
          </el-form>
        </el-col>
        <el-col :offset="2" :span="4">
          <label for="file">
            <el-avatar shape="square" :size="150" fit="cover" :src="user.photo"></el-avatar>
            <p></p>
          </label>
          <input id="file" type="file" hidden ref="file" @change="onFileChange">
        </el-col>
      </el-row>
    </el-card>
    <el-dialog title="" :visible.sync="dialogVisible" append-to-body @opened="onDialogOpened">
      <div class="preview-image-wrap">
        <img class="preview-image" :src="previewImage" ref="preview-image">
      </div>
      <span slot="footer" class="dialog-footer">
        <el-button @click="dialogVisible = false"> </el-button>
        <el-button type="primary" :loading="updatePhotoLoading" @click="onUpdatePhoto"> </el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
import { getUserProfile, updateUserPhoto, updateUserProfile } from '@/api/user'
import 'cropperjs/dist/cropper.css'
import Cropper from 'cropperjs'
import globalBus from '@/utils/global-bus'

export default {
  name: 'SettingsIndex',
  components: {},
  props: {},
  data () {
    return {
      user: {
        email: '',
        id: null,
        intro: '',
        mobile: '',
        name: '',
        photo: ''
      },
      dialogVisible: false, // 
      previewImage: '', // 
      cropper: null, // 
      updatePhotoLoading: false, // loading
      updateProfileLoading: false, // loading
      // 
      formRules: {
        name: [
          { required: true, message: '', trigger: 'blur' },
          { min: 3, max: 5, message: ' 3  5 ', trigger: 'blur' }
        ],
        intro: [
          { required: true, message: '', trigger: 'blur' },
          { min: 1, max: 50, message: '50', trigger: 'blur' }
        ],
        email: [
          { required: true, message: '', trigger: 'blur' },
          { type: 'email', message: '', trigger: ['blur', 'change'] }
        ]
      }
    }
  },
  computed: {},
  watch: {},
  created () {
    this.loadUser()
  },
  mounted () {},
  methods: {
    onUpdateUser () {
      this.updateProfileLoading = true
      // 
      const { name, intro, email } = this.user
      updateUserProfile({
        name,
        intro,
        email
      }).then(res => {
        this.$message({
          type: 'success',
          message: ''
        })
        this.updateProfileLoading = false

        // 
        globalBus.$emit('update-user', this.user)
      })
    },
    loadUser () {
      getUserProfile().then(res => {
        this.user = res.data.data
      })
    },
    onFileChange () {
      // console.log('file change')
      // 
      const file = this.$refs.file

      const blobData = window.URL.createObjectURL(file.files[0])

      this.previewImage = blobData

      // ,
      this.dialogVisible = true

      // change
      this.$refs.file.value = ''
    },
    onDialogOpened () {
      // ing,img,,
      //  DOM 
      const image = this.$refs['preview-image']
      // 
      if (this.cropper) {
        this.cropper.replace(this.previewImage)
        return
      }
      this.cropper = new Cropper(image, {
        aspectRatio: 1,
        viewMode: 1,
        dragMode: 'none',
        cropBoxResizable: false
      })
    },
    onUpdatePhoto () {
      // loading
      this.updatePhotoLoading = true
      // 
      this.cropper.getCroppedCanvas().toBlob(file => {
        const fd = new FormData()
        fd.append('photo', file)
        // 
        updateUserPhoto(fd).then(res => {
          // console.log(res)
          // 
          // this.user.photo = res.data.data.photo
          // 
          this.dialogVisible = false
          // blob
          this.user.photo = window.URL.createObjectURL(file)
          // loading
          this.updatePhotoLoading = false
          this.$message({
            type: 'success',
            message: ''
          })
          // 
          globalBus.$emit('update-user', this.user)
        })
      })
    }
  }
}
</script>

<style scoped lang='less'>
.preview-image-wrap {
  .preview-image {
    display: block;
    max-width: 100%;
    height: 200px;
  }
}
</style>
