<template>
  <div class="image-list">
    <div class="action-head">
      <el-radio-group v-model="collect" size="mini" @change="onCollectChange">
        <el-radio-button :label="false"></el-radio-button>
        <el-radio-button :label="true"></el-radio-button>
      </el-radio-group>
      <el-button v-if="isShowAdd" size="mini" type="success" @click="dialogUploadVisible = true"></el-button>
    </div>

      <!--  -->
      <el-row :gutter="10">
        <el-col :xs="12" :sm="6" :md="6" :lg="4" v-for="(img, index) in images" :key="index" class="image-item" @click.native="selected = index">
          <el-image style="height: 145px;" :src="img.url" fit="cover"></el-image>
          <div class="selected" v-if="isShowSelected && selected === index"></div>
          <div v-if="isShowAction" class="image-action">
            <el-button type="warning" circle size="small" :icon="img.is_collected ? 'el-icon-star-on' : 'el-icon-star-off'" :loading="img.loading" @click="onCollect(img)"></el-button>
            <el-button type="danger" circle size="small" icon="el-icon-delete" :loading="img.loading" @click="onDelete(img)"></el-button>
          </div>
        </el-col>
      </el-row>

      <!--  -->
      <el-pagination style="margin-top:15px;" background layout="prev, pager, next" :total="totalCount" :page-size="pageSize" :current-page.sync="page" @current-change="onCurrentChange"> </el-pagination>
      <!-- , -->
      <el-dialog title="" :visible.sync="dialogUploadVisible" :append-to-body="true">
        <el-upload class="upload-demo" drag action="http://ttapi.research.itcast.cn/mp/v1_0/user/images" :headers="uploadHeaders" name="image" multiple :show-file-list="false" :on-success="onUploadSuccess">
          <i class="el-icon-upload"></i>
          <div class="el-upload__text"><em></em></div>
          <div class="el-upload__tip" slot="tip">jpg/png500kb</div>
        </el-upload>
      </el-dialog>
  </div>
</template>

<script>
import { getImages, collectImage, deleteImage } from '@/api/image'

export default {
  name: 'ImageList',
  components: {},
  props: {
    // 
    isShowAdd: {
      type: Boolean,
      default: true
    },
    // ()
    isShowAction: {
      type: Boolean,
      default: true
    },
    isShowSelected: {
      type: Boolean,
      default: false
    }
  },
  data () {
    const user = JSON.parse(window.localStorage.getItem('user'))
    return {
      collect: false, // 
      images: [], // 
      dialogUploadVisible: false,
      uploadHeaders: {
        Authorization: `Bearer ${user.token}`
      },
      loading: false, // 
      pageSize: 12, // 
      totalCount: 0, // 
      page: 1, // 
      selected: null // 
    }
  },
  computed: {},
  watch: {},
  created () {
    // 
    this.loadImages(1)
  },
  mounted () {},
  methods: {
    // 
    loadImages (page = 1) {
      // ,
      this.page = page
      getImages({
        collect: this.collect,
        page,
        per_page: this.pageSize
      }).then(res => {
        // console.log(res)
        const results = res.data.data.results
        results.forEach(img => {
          // loading
          img.loading = false
        })
        this.images = results
        this.totalCount = res.data.data.total_count
      })
    },

    onCollectChange (value) {
      this.loadImages(1, value)
    },

    onUploadSuccess () {
      // 
      this.dialogUploadVisible = false
      // 
      this.loadImages(this.page)
      this.$message({
        type: 'success',
        message: ''
      })
    },

    onCurrentChange (page) {
      // 
      this.loadImages(page)
    },

    onCollect (img) {
      // loading
      img.loading = true
      collectImage(img.id, !img.is_collected).then(res => {
        // 
        img.is_collected = !img.is_collected
        // loading
        img.loading = false
      })
    },

    onDelete (img) {
      img.loading = true
      deleteImage(img.id).then(res => {
        // 
        this.loadImages(this.page)
        img.loading = false
      })
    }
  }
}
</script>

